//     api

export class Users {
    static login(payload: loginType) {

        const data = {
            email: "1@mail.ru",
            password: "12345678"
        }

        //   login      
        //  resolve  reject
        //       

        return new Promise((resolve, reject) => {
            if (payload.email === data.email && payload.password === data.password) {
                setTimeout(() => {
                    resolve({
                            data: {
                                isLoggedIn: true
                            }
                        }
                    )
                }, 1000)

            } else {
                setTimeout(() => {
                    reject('   ')
                }, 1000)

            }
        })
    }
}

type loginType = {
    email: string
    password: string
}

